import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AjoutLivreComponent } from './ajout-livre/ajout-livre.component';
import { GestionMembreComponent } from './gestion-membre/gestion-membre.component';
import { GestionPageComponent } from './gestion-page/gestion-page.component';
import { ListeLivreComponent } from './liste-livre/liste-livre.component';
import { MenuComponent } from './menu/menu.component';

const routes: Routes = [
  {path:'ajoutLivre',component:AjoutLivreComponent},
  {path:'',component:AjoutLivreComponent},
{path:"listLivre",component:ListeLivreComponent},

{path:'gestionmembre',component:GestionMembreComponent}
,{path:'',component:GestionMembreComponent}
,

{path:'gestionpage',component:GestionPageComponent}

,{
  path:'menu',component:MenuComponent
}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
